<template>
  <div class="design-config">
    <div class="config-tips">
      <div class="config-size"></div>
      <div @click="showDialog = true" class="config-edit"></div>
    </div>
    <div class="config-view">
      <div @click="showDialog = true" class="config-width">
        <span class="config-width-value">{{width}}{{unit}}</span>
        <span class="config-width-label"></span>
      </div>
      <div @click="showDialog = true" class="config-height">
        <span class="config-heigth-value">{{height}}{{unit}}</span>
        <span class="config-heigth-label"></span>
      </div>
    </div>
    <transition name="fade">
      <div v-if="showDialog" class="config-dialog">
        <div class="config-dialog-main">
          <div class="config-dialog-canvas">
            <vx-canvas-preview type="preview" v-if="showDialog" :w="width" :h="height" :width="560" :height="504" />
          </div>
          <div class="config-dialog-aside">
            <div class="config-title">
              <div class="config-dialog-title"></div>
              <div class="config-dialog-close">
                <i @click="showDialog = false" class="el-icon-close"></i>
              </div>
            </div>
            <div class="config-dialog-select">
              <ul>
                <li
                  v-for="(item,index) in selectList"
                  :key="index"
                  :class="{'config-dialog-selected':sel==index}"
                  @click="changeSelHandler(index,item)"
                >
                  <div class="config-select-lable">{{item.title}}</div>
                  <div v-if="item.width == originalW && item.height == originalH"></div>
                </li>
              </ul>
            </div>
            <div class="config-dialog-action">
              <el-input-number
                :max="9000"
                :min="20"
                :controls="false"
                @change="changeWidthHandler"
                v-model="width"
                placeholder=""
              />
              <vx-icon @click.native="lockHandler" :name="lockIcon" />
              <el-input-number
                :controls="false"
                :max="12000"
                :min="20"
                @change="changeHeightHandler"
                v-model="height"
                placeholder=""
              />
              <el-select v-model="unit" @change="unitChangeHandler" placeholder="">
                <el-option
                  v-for="item in selectOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </div>
            <div class="config-dialog-info"></div>
            <div class="config-dialog-checkout">
              <!-- <el-checkbox v-model="checked"></el-checkbox> -->
            </div>
            <div class="config-dialog-space"></div>
            <div @click="changeSizeHandler" class="config-dialog-btn"></div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>
<script>
import { Message } from 'element-ui'
import { state, mitation, getter } from 'src/design/store'
import util from 'src/design/utils'
import { VxCanvasPreview } from 'src/design/view/canvas'
export default {
  name: 'vx-canvasSize',
  components: {
    VxCanvasPreview
  },
  data() {
    return {
      showDialog: false, //
      lockIcon: 'Unlocked', //
      // checked: false, //
      sel: 0, //
      width: 1242, //
      height: 2208, //
      originalW: 0, //
      originalH: 0, //
      aspectRatio: null, //
      unit: 'px', //
      zoom: 0,
      selectOptions: [
        {
          value: 'px',
          label: 'px()'
        },
        {
          value: 'cm',
          label: 'cm()'
        },
        {
          value: 'mm',
          label: 'mm()'
        }
      ],
      selectList: [
        {
          title: '',
          label: '',
          width: null,
          height: null
        },
        {
          title: ' 1242px  2208px ',
          label: '',
          width: 1242,
          height: 2208
        },
        {
          title: ' 800px  2000px ',
          label: '',
          width: 800,
          height: 2000
        },
        {
          title: '/ 1248px  2208px ',
          label: '',
          width: 1248,
          height: 2208
        },
        {
          title: ' 1242px  2208px ',
          label: '',
          width: 1242,
          height: 2208
        },
        {
          title: ' 1242px  1242px ',
          label: '',
          width: 1242,
          height: 1242
        },
        {
          title: ' 1242px  2208px ',
          label: '',
          width: 1242,
          height: 2208
        }
      ]
    }
  },
  computed: {
    canvasW() {
      return getter.canvasW()
    },
    canvasH() {
      return getter.canvasH()
    }
  },
  created() {
    this.originalW = getter.canvasW()
    this.originalH = getter.canvasH()
    this.selectList.forEach((element, index) => {
      if (
        this.originalW === element.width &&
        this.originalH === element.height
      ) {
        this.sel = index
      }
    })
  },
  methods: {
    //
    lockHandler() {
      if (this.lockIcon === 'Unlocked') {
        this.aspectRatio = this.width / this.height
        this.lockIcon = 'lock'
      } else if (this.lockIcon === 'lock') {
        this.lockIcon = 'Unlocked'
      }
    },
    //
    unitChangeHandler(value) {
      //px
      if (value === 'px') {
        this.width = this.canvasW
        this.height = this.canvasH
        this.unit = 'px'
        state.global.unit = 'px'
      }
      //cm
      else if (value === 'cm') {
        this.width = Math.round((this.canvasW * 25.4) / state.global.dpi / 10)
        this.height = Math.round((this.canvasH * 25.4) / state.global.dpi / 10)
        this.unit = 'cm'
        state.global.unit = 'cm'
      }
      //mm
      else if (value === 'mm') {
        this.width = Math.round((this.canvasW * 25.4) / state.global.dpi)
        this.height = Math.round((this.canvasH * 25.4) / state.global.dpi)
        this.unit = 'mm'
        state.global.unit = 'mm'
      }
    },
    //
    changeSelHandler(index, item) {
      this.sel = index
      this.unit = 'px'
      if (index !== 0) {
        this.width = item.width
        this.height = item.height
      }
    },
    //
    changeSizeHandler() {
      if (parseInt(this.width) != this.width) {
        Message({
          message: '',
          type: 'error',
          duration: 5 * 1000 // (ms)
        })
        return
      } else if (parseInt(this.height) != this.height) {
        Message({
          message: '',
          type: 'error',
          duration: 5 * 1000 // (ms)
        })
        return
      }
      if (this.unit === 'px') {
        mitation.setCanvasSize(this.width, this.height)
      } else if (this.unit === 'mm') {
        mitation.setCanvasSize(
          Math.round((this.width * state.global.dpi) / 25.4),
          Math.round((this.height * state.global.dpi) / 25.4)
        )
      } else if (this.unit === 'cm') {
        mitation.setCanvasSize(
          Math.round((this.width * 10 * state.global.dpi) / 25.4),
          Math.round((this.height * 10 * state.global.dpi) / 25.4)
        )
      }
      this.showDialog = false
      this.updateSizeHandler()
    },
    //
    changeWidthHandler() {
      this.sel = 0
      if (this.lockIcon === 'lock') {
        this.height = util.floatObj.divide(this.width, this.aspectRatio, 3)
      }
    },
    //
    changeHeightHandler() {
      this.sel = 0
      if (this.lockIcon === 'lock') {
        this.width = util.floatObj.multiply(this.height, this.aspectRatio, 3)
      }
    },

    // 
    updateSizeHandler() {
      let h = state.layout.height,
        w = state.layout.width
      if (h < w) {
        this.zoom = 560 / w
      } else {
        this.zoom = 504 / h
      }
    }
  },
  mounted() {
    this.updateSizeHandler()
  }
}
</script>