<template>
  <div>
    <div class="vx-login-form-top"></div>
    <div class="vx-login-form-phone">
      <el-input placeholder="" v-model="username" class="input-with-select">
        <el-select v-model="number" slot="prepend" placeholder="">
          <el-option label="+86" value=" +86"></el-option>
          <el-option label="+852" value=" +852"></el-option>
          <el-option label="+853" value=" +853"></el-option>
        </el-select>
      </el-input>
    </div>
    <div v-if="messageLogin" class="vx-login-form-pass">
      <el-input type="password" placeholder="" v-model="passWord">
        <template slot="prepend"></template>
      </el-input>
    </div>
    <div v-if="!messageLogin" class="vx-login-form-msg">
      <el-input type="text" placeholder=""></el-input>
      <div class="vx-login-form-getVeriy"></div>
    </div>
    <div class="vx-login-phone-content">
      <div @click="messageLogin = !messageLogin" class="vx-login-phone-message">
        <span v-if="messageLogin"></span>
        <span v-if="!messageLogin"></span>
      </div>
      <div @click="forgetPasswordHandler"></div>
    </div>
    <div @click="loginHandler" class="vx-login-phone-login"></div>
  </div>
</template>
<script>
// import { API } from 'src/design/utils'
// import { mitation } from 'src/design/store'
// import { mutations } from 'src/store'
import { authLoginByAccount } from 'src/api/api'
import MyStorage from 'src/utils/cache'

export default {
  name: 'vx-phoneLogin',
  data() {
    return {
      username: '',
      number: '+86',
      passWord: '',
      messageLogin: true
    }
  },
  computed: {},
  methods: {
    forgetPasswordHandler() {
      this.$emit('forgetPassword')
    },
    loginHandler() {
      let param = {
        username: this.username,
        password: this.passWord
      }
      //
      authLoginByAccount(param).then(res => {
        if (res.errno === 0) {
          MyStorage.setItem('Token', res.data.token)
          MyStorage.setItem('userName', res.data.userInfo.nickName)
          this.$store.commit('updateLoginState', true)
        } else {
          this.$message({
            message: res.errmsg,
            type: 'error'
          })
        }
      })
    }
  }
}
</script>