import MyStorage from "src/utils/cache"
import axios from 'axios';
import { Message } from 'element-ui';
import util from 'src/design/utils';
// const commonData = {

// };
// 
function errorCreate(msg) {
    const err = new Error(msg)
    errorLog(err)
    throw err
}
function errorLog(err) {
    // 
    if (process.env.NODE_ENV === 'development') {
        util.log.danger('>>>>>> Error >>>>>>');

    }
    // 
    Message({
        message: err.message,
        type: 'error',
        duration: 5 * 1000 // (ms)
    });
}


// 'http://111.67.205.205:18080/wx'
//  axios 
const Axios = axios.create({
    baseURL: 'http://111.67.205.205:18080/wx/',
    timeout: 5000 // 
});

const Upload = axios.create({
    baseURL: 'http://111.67.205.205:18080/wx/',
    timeout: 5000,
})
/**
 * 
 * @param {*} config 
 */
const requestFn = config => {
    // 
    // config.data = {...commonData, ...config.data }; // 
    if (!config.headers['X-Litemall-Token']) {
        config.headers['X-Litemall-Token'] = `${MyStorage.getItem('Token') || ''}`;
    }
    return config
}
/**
 * 
 * @param {*} config 
 */
const requestErrFn = error => {
    errorLog(error);
    Promise.reject(error);
}
/**
 * 
 * @param {*} response 
 */
const responseFn = response => {
    // dataAxios  axios  data
    const dataAxios = response.data
    // 
    const { errno } = dataAxios
    //  code 
    if (errno === undefined) {
        return dataAxios;
    } else {
        switch (errno) {
            case 400:
                errorCreate(`${dataAxios.errmsg}`);
                break;
            case 403: errorCreate(`${dataAxios.errmsg}`); break
            case 404: errorCreate(`${dataAxios.errmsg}`); break
            case 408: errorCreate(`${dataAxios.errmsg}`); break
            case 500: errorCreate(`${dataAxios.errmsg}`); break
            case 501: errorCreate(`${dataAxios.errmsg}`); break
            case 502: errorCreate(`${dataAxios.errmsg}`); break
            case 503: errorCreate(`${dataAxios.errmsg}`); break
            case 504: errorCreate(`${dataAxios.errmsg}`); break
            case 505: errorCreate(`${dataAxios.errmsg}`); break
            default:
                return dataAxios;
        }
    }
}
/**
 * 
 * @param {*} error 
 */
const responseErrFn = error => {
    if (error && error.response) {
        switch (error.response.status) {
            case 400: error.message = ''; break
            // case 401: error.message = ''; break
            case 403: error.message = ''; break
            case 404: error.message = `: ${error.response.config.url}`; break
            case 408: error.message = ''; break
            case 500: error.message = ''; break
            case 501: error.message = ''; break
            case 502: error.message = ''; break
            case 503: error.message = ''; break
            case 504: error.message = ''; break
            case 505: error.message = 'HTTP'; break
            default: break
        }
    }
    errorLog(error);
    return Promise.reject(error);
}
// 
Axios.interceptors.request.use(
    requestFn,
    requestErrFn
);
// 
Axios.interceptors.response.use(
    responseFn,
    responseErrFn
);
Upload.interceptors.response.use(
    responseFn,
    responseErrFn
)
export { Axios, Upload };
export default Axios
